import React, {Component} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import rescuer from '../imgs/1.jpeg'

class Result extends React.Component{
    constructor(props){
        super(props);
    }
    activateToogle =()=>{
        alert('john')
    }
    render(){
        return(
            <div className="result search">
                        <div className="toggle" onClick={this.activateToogle}/>



                        <div className="result-info">

                            <div className="contact-info">
                                <div className="contact-info-header">
                                    <h2 style={{fontSize: "20px"}}>Rescuer is 11mins {this.props.disTime} away</h2>
                                </div>
                                <div className="hsj">
                                    <div className="avatar rescuer">
                                        <img src={rescuer}/>
                                    </div>
                                    <h3 style={{textAlign: "center"}}>St. Matthew's Hospital</h3>
                                    <div className="details"><span><FontAwesomeIcon icon="phone-alt" style={{fontSize: "1.2em"}} /></span><p>{this.props.phone}+(01)211 2382 282</p></div>
                                    <div className="details"><span><FontAwesomeIcon icon="map-marker-alt" style={{fontSize: "1.2em"}} /></span><p>{this.props.address}12b, long long log, address</p></div>
                                    <div className="details"><span><FontAwesomeIcon icon="envelope" style={{fontSize: "1.2em"}} /></span><p>{this.props.email}manyemail@things.com</p></div>
                                </div>
                            </div>

                            <div className="more-result">
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>
                            <div className="pre-res"><span className="r-icon"><FontAwesomeIcon icon="store" style={{fontSize: "1em"}} /></span><p>{this.props.rescueName}St. Matthew Things</p></div>                                  
                            </div>
                        </div>
                    </div>
        )
    }
}
export default Result
