import React, {Component} from 'react';
import avatar from '../imgs/1.jpeg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class Sidebar extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <aside>
                    <div className="profile">
                        <div className="avatar"><img src={this.props.avatar}/><span/></div>
                        <h2>Hello {this.props.frName}</h2>
                    </div>
                    <div className="nav">
                        <div style={{borderTop:"2px solid #a3a1a1"}}className="nav-content">
                            <span className="r-icon"><FontAwesomeIcon icon="list-alt" /></span>
                            <p>Requests</p>
                        </div>
                        <div className="nav-content">
                            <span className="r-icon"><FontAwesomeIcon icon="comments" /></span>
                            <p>Messages</p>
                        </div>
                        <div className="nav-content">
                            <span className="r-icon"><FontAwesomeIcon icon="user" /></span>
                            <p>Settings</p>
                        </div>
                    </div>
                </aside>
        )
    }
}
export default Sidebar