import React, {Component} from 'react';
import GoogleMapReact from 'google-map-react';
import loc from '../imgs/1.jpeg'
const Marker = props => {
  return <>
    <div className="pin"></div>
    <div className="pulse"></div>
  </>
}

class Map extends React.Component{
    static defaultProps = {
        center: {
          lat: 45.421532,
          lng: -75.697189
        },
        zoom: 11
      };
    render(){
        return(
            <div className="map">
                <GoogleMapReact
          bootstrapURLKeys={{ key: "AIzaSyB8Hxd8E1RntZLTceMKgXIeN3vrrfyV4jE" }}
          defaultCenter={this.props.center}
          defaultZoom={this.props.zoom}
        >
          <Marker
            lat={45.421532}
            lng={-75.697189}
          />
        </GoogleMapReact>
        

            </div>
        )
    }
}
export default Map
